body {
  background-color: #eee;
}

.Wrapper {
  max-width: 600px;
  margin: auto;
}

.Inner {
  margin: 3rem 0;
  padding: 0 15px;
}

.Item {
  padding: 2em;
  border-radius: 3px;
  background-color: #fff;
  &:not(:first-of-type) {
    margin-top: 2rem;
  }
}

/* フォーカスや音声読み上げは残したまま非表示にする。 */
.Checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.Checkbox + label {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

/* 擬似要素で楕円を作る。 */
.Checkbox + label:before {
  content: "";
  display: inline-block;
  $size: 1em;
  width: $size;
  height: $size;
  margin-right: 0.25em;
  border: 1px solid #767676;
  border-radius: 3px;
  vertical-align: text-bottom;
  background-repeat: no-repeat;
  background-size: contain;
}

/* フォーカスが当たると楕円の色などを変える。 */
.Checkbox:focus + label:before {
  border-color: #1589ee;
  box-shadow: 0 0 5px #1589ee;
}

/* チェックボックスがチェックされるとボーダーは非表示、画像でチェックボックスを表示する。 */
.Checkbox:checked + label:before {
  border: 0;
  background-image: url("../img/Checkbox_on.svg");
}

/* disabled（非活性）時にはスタイルを変える。 */
.Checkbox:disabled,
.Checkbox + label[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

.Radio {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.Radio + label {
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.Radio + label:before {
  content: "";
  $size: 1em;
  width: $size;
  height: $size;
  display: inline-block;
  margin-right: 0.25em;
  border: $form-border;
  border-radius: 50%;
  vertical-align: text-bottom;;
  background-repeat: no-repeat;
  background-size: contain;
}

.Radio:focus + label:before {
  border-color: #1589ee;
  border-radius: 50%;
  box-shadow: 0 0 5px #1589ee;
}

.Radio:checked + label:before {
  border: 0;
  background-image: url("../img/Radio_on.svg");
}

.Radio:disabled,
.Radio + label[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}
