  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

/* #base
   -------------------------------------------------------------------------- */
/**
 * JavaScriptでブレイクポイントを取得するために指定しています。
 * /assets/js/lib/breakpoint.js を参照してください。
 */
body:before {
  content: "";
  display: none;
}

@media (min-width: 768px) {
  body:before {
    content: "md";
  }
}

@media (min-width: 1080px) {
  body:before {
    content: "lg";
  }
}

@media (min-width: 1440px) {
  body:before {
    content: "xl";
  }
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  font-size: 87.5%;
  line-height: 1.5;
}

@media (min-width: 768px) {
  html {
    font-size: 100%;
  }
}

body {
  color: #000;
  font-family: "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: auto;
  font-kerning: normal;
  background-color: #fff;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

/**
 * `dt`と`dd`の左端を揃えます。
 */
dd {
  margin-left: 0;
}

/**
 * 日本語では斜体を使用しないためリセットします。
 */
[lang="ja"] i,
[lang="ja"] cite,
[lang="ja"] em,
[lang="ja"] address,
[lang="ja"] dfn {
  font-style: normal;
}

/**
 * フルードイメージにします。
 */
img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

/* imgとonject要素でSVGをフルードイメージにします。 */
[src$=".svg"],
[data$=".svg"] {
  width: 100%;
  /* IE対応 */
}

code {
  font-family: Consolas, Menlo, Courier, monospace;
  font-size: 0.85em;
}

/* IE10以降`input[type="text"]`の入力内容の消去を非表示にします。 */
input::-ms-clear {
  visibility: hidden;
}

/* IE10以降の`input[type="password"]`のマスクの一時的解除を非表示にします。 */
input::-ms-reveal {
  visibility: hidden;
}

/* Normalize.cssをリセットします。 */
figure {
  margin: 0;
}

/* ブロック要素の上下のマージンをリセットします。 */
h1, h2, h3, h4, h5, h6,
ul, ol, dl,
blockquote, p, address,
hr,
table,
fieldset, figure,
pre {
  margin-top: 0;
  margin-bottom: 0;
}

body {
  background-color: #eee;
}

.Wrapper {
  max-width: 600px;
  margin: auto;
}

.Inner {
  margin: 3rem 0;
  padding: 0 15px;
}

.Item {
  padding: 2em;
  border-radius: 3px;
  background-color: #fff;
}

.Item:not(:first-of-type) {
  margin-top: 2rem;
}

/* フォーカスや音声読み上げは残したまま非表示にする。 */
.Checkbox {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.Checkbox + label {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

/* 擬似要素で楕円を作る。 */
.Checkbox + label:before {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  margin-right: 0.25em;
  border: 1px solid #767676;
  border-radius: 3px;
  vertical-align: text-bottom;
  background-repeat: no-repeat;
  background-size: contain;
}

/* フォーカスが当たると楕円の色などを変える。 */
.Checkbox:focus + label:before {
  border-color: #1589ee;
  box-shadow: 0 0 5px #1589ee;
}

/* チェックボックスがチェックされるとボーダーは非表示、画像でチェックボックスを表示する。 */
.Checkbox:checked + label:before {
  border: 0;
  background-image: url("../img/Checkbox_on.svg");
}

/* disabled（非活性）時にはスタイルを変える。 */
.Checkbox:disabled,
.Checkbox + label[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}

.Radio {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  overflow: hidden;
  padding: 0;
  clip: rect(0, 0, 0, 0);
}

.Radio + label {
  display: inline-block;
  position: relative;
  cursor: pointer;
}

.Radio + label:before {
  content: "";
  width: 1em;
  height: 1em;
  display: inline-block;
  margin-right: 0.25em;
  border: 1px solid #767676;
  border-radius: 50%;
  vertical-align: text-bottom;
  background-repeat: no-repeat;
  background-size: contain;
}

.Radio:focus + label:before {
  border-color: #1589ee;
  border-radius: 50%;
  box-shadow: 0 0 5px #1589ee;
}

.Radio:checked + label:before {
  border: 0;
  background-image: url("../img/Radio_on.svg");
}

.Radio:disabled,
.Radio + label[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}
